.advantage{
  &-object{
    &__grid{
      display: grid;
      grid-template-columns: repeat(4,minmax(0,1fr));
      grid-auto-rows: 285px;
      gap: 25px;
    }
    &__item{
      width: 100%;
      height: 100%;
      &-wrapper{
        padding: 18px;

        height: 100%;
        width: 100%;

        position: relative;
        &::before{
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          z-index: 2;

          width: 100%;
          height: 100%;
          display: block;

          background-color: rgba(#000,.5);
        }
      }
      &-content{
        border:1px solid #fff;

        width: 100%;
        height: 100%;

        position: relative;
        z-index: 3;
        &:hover{
          cursor: pointer;
          .front{
            opacity: 0;
          }
          .back{
            opacity: 1;
          }
        }
      }
      &-img{
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;

        width: 100%;
        height: 100%;
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &-front{
        display: flex;
        align-items: center;
        justify-content: center;

        width: 100%;
        height: 100%;

        padding: 0 72px;

        transition: .5s opacity;

        position: absolute;
        left: 0;
        top: 0;
      }
      &-back{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;

        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        opacity: 0;

        transition: .5s opacity;

        background-color: #fff;

        padding: 7px;
      }
      &-title{
        font-size: 20px;
        line-height: 26px;
        font-weight: 500;

        color:#fff;

        text-align: center;
      }
      &-subTitle{
        font-size: 18px;
        line-height: 24px;
        font-weight: 500;

        color:#262626;

        text-align: center;

        padding: 0 5px 15px 5px;

        margin-bottom: 15px;

        position: relative;
        &::after{
          content: '';
          position: absolute;
          left: calc(50% - 32px);
          bottom: 0;

          width: 64px;
          height: 1px;

          background-color: #b6923d;
        }
      }
      &-text{
        font-size: 13px;
        line-height: 16px;

        color:#262626;
        text-align: center;
      }
      &-info{
        border:1px solid #b6923d;

        padding: 18px 20px;

        width: 100%;
        height: 100%;
      }
    }
  }
}

.object{
  &-slider{
    height: 100%;
    width: 100%;
    .swiper-pagination{
      position: relative;
      bottom: unset;
      left: unset;

      display: flex;
      align-items: flex-start;

      margin-top: 50px;
      &-bullet{
        opacity: .5;
        background-color: #f8f8f8;
        &-active{
          opacity: 1;
        }
      }
    }
    .swiper-button{
      &-next,&-prev{
        z-index: 4;
      }
      &-prev{
        width: 30px;
        height: 70px;

        background: url('../img/arrow-white.png') center center/contain no-repeat;

        left: 90px;
        top: calc(50% - 35px - 75px);
      }
      &-next{
        width: 30px;
        height: 70px;

        background: url('../img/arrow-white.png') center center/contain no-repeat;

        right: 90px;
        top: calc(50% - 35px - 75px);

        transform: rotate(180deg);
      }
    }
    &__slide{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &__img{
      height: calc(100% - 150px);
      width: 100%;
      position: relative;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
    &__content{
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;

      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__title{
      font-size: 44px;
      font-weight: 700;
      line-height: 55px;
      color:#fff;
    }
    &__text{
      font-size: 24px;
      color:#fff;
      line-height: 40px;
    }
    &__info{
      width: 100%;
      background-color: #262626;
      &-wrapper{
        display: flex;
        align-items: center;

        height: 150px;
        padding: 30px 0;
      }
      &-logo {
        width: 180px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;

        flex-shrink: 0;

        padding-right: 50px;

        border-right: 1px solid rgba(#dedfe4,.08);

        img {
          width: 100%;
          height: 100%;

          object-fit: contain;
        }
      }
      &-content{
        padding-left: 40px;
      }
      &-footer{
        margin-left: auto;
      }
      &-btn{
        width: 270px;
        height: 45px;

        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;

        background-color: #be983e;

        color:#fefefe;
        font-size: 14px;
        font-weight: 700;
      }
    }
    &__adv{
      display: flex;
      align-items: center;
      flex-wrap: wrap;

      margin: -26px -30px;

      max-width: 700px;
      width: 100%;
      &-item{
        margin: 13px 15px;
        &-label{
          font-size: 16px;
          color:#fff;
          span{
            font-size: 24px;
            color:#be983e;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1440px) {
  .section-object__wrapper{
    padding: 0;

    align-items: flex-start;
    height: 100%;
    min-height: auto;
  }

  .object{
    &-slider{
      &__img{
        height: 550px;
      }

      &__content{
        padding:0 45px;
      }

      .swiper{
        &-button{
          &-next{
            right: 10px;
          }
          &-prev{
            left: 10px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .advantage{
    &-object{
      &__grid{
        grid-template-columns: repeat(3,minmax(0,1fr));
      }
    }
  }
  .object{
    &-slider{
      &__info{
        &-wrapper{
          height: auto;
          flex-wrap: wrap;
        }
        &-logo{
          flex: 100%;

          margin-bottom: 40px;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .advantage{
    &-object{
      &__grid{
        grid-template-columns: repeat(2,minmax(0,1fr));
      }
    }
  }
  .object{
    &-slider{
      &__info{
        &-wrapper{
          height: auto;
          flex-wrap: wrap;

          flex-direction: column;
        }
        &-logo{
          padding-right: 0;
        }
        &-content{
          padding-left: 0;
          border: none;
        }
        &-footer{
          margin: 40px auto 0 auto;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .advantage{
    &-object{
      &__grid{
        grid-template-columns: 1fr;
      }
    }
  }
  .object{
    &-slider{
      .swiper-pagination{
        margin-top: 20px;
      }
      .swiper-button{
        &-next,&-prev{
          top: 250px;
        }
      }
      &__adv{
        margin: 0;
        padding: 0 15px;

        flex-direction: column;
        &-item{
          margin: 0;
        }
        &-item + &-item{
          margin-top: 10px;
        }
      }
      &__info{
        &-footer{
          margin-top: 20px;
        }
        &-logo{
          margin-bottom: 20px;
        }
      }
      &__title{
        font-size: 24px;
        line-height: 1;
      }
      &__text{
        font-size: 18px;
        line-height: 1;

        margin-top: 20px;
      }
    }
  }
}