.section{
  &__wrapper{
    padding: 75px 0;
  }
  &__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: 24.5px;
    font-weight: 700;

    color:#262626;
  }
  &__desc{
    font-size: 14px;
    line-height: 19px;

    color:#262626;

    text-align: justify;
    p + p{
      margin-top: 15px;
    }
  }
  &__link{
    font-size: 14px;
    color:#262626;
    text-decoration: underline;
    text-underline-position: under;
    text-decoration-color: #be983e;
  }
  &__header + &__content{
    margin-top: 44px;
  }
}

.section-achiv__content{
  position: relative;
  .swiper-button-next{
    width: 30px;
    height: 13px;

    background: url("../img/layout-arrow.png") center center/contain no-repeat;

    transform: rotate(-90deg);

    top: calc(50% + 6.5px);
    right: -50px;
  }
}

.achievements-slider{
  &-big{
    position: relative;

    height: 285px;
    &__item{
      width: 100%;
      height: 100%;
      &-img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

.page-tabs{
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;

    max-width: 550px;
    width: 100%;

    padding: 30px 0;
    &-item{
      flex-shrink: 0;
      &.active{
        .page-tabs{
          &__nav{
            &-link{
              text-decoration-color: transparent;
              color:#be983e;
            }
          }
        }
      }
    }
    &-link{
      font-size: 16px;
      color:#262626;

      text-decoration: underline;
      text-underline-position: under;
      text-decoration-color: #be983e;

      transition: .3s color, .3s decoration-color;
    }
  }
}

@media screen and (max-width: 1140px) {
  .section-achiv__content{
    .swiper-button-next{
      display: none;
    }
  }
}

@media screen and (max-width: 991px) {
  .section{
    &__wrapper{
      padding: 30px 0;
    }
  }
}

@media screen and (max-width: 600px) {
  .page-tabs{
    &__nav{
      flex-wrap: wrap;
      margin: -10px;
      &-item{
        margin: 10px;
      }
    }
  }
}

@media screen and (max-width: 475px) {
  .section{
    &__title{
      font-size: 18px;
    }
    &__header + &__content{
      margin-top: 15px;
    }
  }
  .achievements-slider {
    &-big {
      height: 235px;
    }
  }
}